section {
  padding: 10px 0;
  width: 95%;
  margin: auto;
}

section h2 {
  font-size: 30px;
  padding: 0px 0 25px 0;
}

section ul li {
  list-style: none;
  text-align: start;
  border: 1px solid #764A3B;
  border-radius: 15px;
  padding: 0px 13px 5px 10px;
  display: flex;
  margin: 15px 0 15px 0;
  justify-content: space-between;
}

span {
  font-family: "Myriad-SemiBold";
  font-size: 20px;
  line-height: 50px;
}

img {
  border-radius: 10px;
  margin: 13px 0 0 25px;
}

.preço {
  font-family: "Myriad-SemiBold";
  margin: 13px 0 2px 0;
  font-size: 18px;
}

.noImage {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}